import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class ItemsService {
    getCategory(){
      let categoryList=["Stationary","Food","Electronics"];
      return categoryList;
    }
    getName(){
      let nameList=["pen","applejuice","lapetop"];
      return nameList;
    }
    getPrice(){
      let priceList=["50","60","60000"];
      return priceList;
    }
  constructor() { }
}
